@import "@/assets/global.scss";















































.head {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  > span {
    user-select: none;
  }
}

.listContent {
  height: 100%;
  max-height: 172px;
  width: 100%;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  > div {
    display: flex;
    padding: 8px 14px 8px 0;
  }
}

.txtContent {
  width: 162px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 8px;

  > span {
    text-align: left;
  }

  > .limit-lines {
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}
