@import "@/assets/global.scss";







































































































































































.head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  width: 100%;
  max-width: $max-width;
  margin: 0 auto;
  padding: 0 20px;
}

.head > img {
  //position: absolute;
  //top: 50%;
  //right: 22px;
  //transform: translate(0, -50%);
  cursor: pointer;
}

.content {
  width: 100%;
  max-width: $max-width;
  padding: 0 16px 60px 16px;
  margin: 0 auto;
  box-sizing: border-box;
}

.midContent {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 32px 0;

  > .midItem {
    display: flex;
    align-items: center;
    flex-direction: column;
    > div {
      background: #F2F2F2;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
